@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}
a {
  text-decoration: none;
  color: #fff;
}
ul {
  list-style: none;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: black;
}
@layer components {
  .section-style {
    @apply w-full relative  h-screen shrink-0 bg-black overflow-auto;
  }
  .container-style {
    @apply w-full h-full py-[2rem] md:py-[4.5rem] px-[1rem] md:px-[3rem] lg:px-[4rem] lg:w-[85vw] 2xl:px-[3rem] m-auto max-w-[1920px];
  }
  .sub-card {
    @apply bg-[#0a0a0a] transition-all duration-500 backdrop:blur-xl border-white/20 rounded-md overflow-hidden shadow-[inset_0_0_20px_rgba(255,255,255,0.05)];
  }
  .skill-card {
    @apply p-1 px-2 border text-[#bbb] bg-[#111] hover:border-[#CE6486] hover:text-[#CE6486] border-white/10 rounded-md;
  }
}
::selection {
  color: rgb(255, 255, 255);
  background: rgb(206, 100, 134);
}
::-webkit-scrollbar {
  display: none;
}

/* Dahy */
.shadow-circle {
  width: 12px;
  height: 12px;
  transition: box-shadow 0.3s ease;
  border-radius: 2px;
  background: transparent;
  box-shadow: inset 0 0 0 2px rgba(206, 100, 134, 0.5);
}

.border-stylish {
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.05);
  &:hover {
    border-image: linear-gradient(to right, #ff450077, #00ffff77, #ffa50077);
    border-image-slice: 1;
    box-shadow: 0 0 15px rgba(0, 255, 255, 0.25);
  }
}
.button-card {
  /* background: linear-gradient(135deg, #ff867c, #e880a0); */
  background: linear-gradient(135deg, #ff867c, rgb(206, 100, 134));

  transition: all 0.5s ease;
  border-radius: 8px;
  padding: 4px 16px;
  color: #f1f1f1;
  &:hover {
    background: linear-gradient(135deg, #ffaaa5, #fbb1bd);
    box-shadow: 0 0 20px rgba(0, 255, 255, 0.4);
  }
}

/* Preloader */

.preloader .line {
  display: inline-block;
  animation: fade 1000ms infinite ease-in-out, glitch 200ms infinite ease-in-out;
}
span .line {
  animation: glitch 1000ms infinite ease-in-out;
}
.preloader .line-1 {
  animation-delay: 200ms;
}
.preloader .line-2 {
  animation-delay: 400ms;
}
.preloader .line-3 {
  animation-delay: 600ms;
}
.preloader .line-4 {
  animation-delay: 800ms;
}
.preloader .line-6 {
  animation-delay: 1000ms;
}
.preloader .line-7 {
  animation-delay: 1200ms;
}
.preloader .line-8 {
  animation-delay: 1400ms;
}
.preloader .line-9 {
  animation-delay: 1600ms;
}
.preloader .line-10 {
  animation-delay: 1800ms;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  7.7% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes glitch {
  33% {
    transform: translateY(1px);
    text-shadow: 6px 2px 5px red;
  }
  67% {
    text-shadow: -6px 2px 5px blue;
    transform: translateY(-1px);
  }
  100% {
    text-shadow: 6px 2px 5px green;
    transform: translateY(0px);
  }
}
